body {
  margin : 0;
  padding : 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
  box-sizing : border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root{
  --color1 : #df153e;
  --color2 : #000000;
  --color3 : #ffffff;
  --color4 : #808080;
  --color5 : #717171;
  --color6 : rgb(35, 35, 35);
}
/* ------------------ NAVBAR ---------------- */
.header{
  display : flex;
  padding : 0rem 1rem 0rem 1rem;
  justify-content : space-between;
  overflow : hidden;
  background-color : var(--color2);
  position : sticky; 
  top : 0;
}
.header .logo{
  font-size : 3rem;
  font-weight : bold;
  color : var(--color4);
}
.header .menu{
  display : flex;
  align-items : center;
}
.header .menu li{
  list-style-type : none;
  padding-right : 1.25rem;
}
.header .menu li .anc{
  text-decoration : none;
  font-weight : bold;
  color : var(--color1);
}
.header .menu li .anc:hover{
  color : var(--color3);
}
/* ----------------- Footer ----------------- */
.footer{
  font-size: 1rem;
  font-weight: bold;
  padding : 0.5rem 1rem 0.75rem 1rem;
  color : var(--color1);
  background-color : var(--color2);
  position : fixed;
  bottom : 0;
  left : 0;
  right : 0;
}
/* --------------- Home Page -------------- */
.Container{
  background-color : var(--color4);
}
.Container .Content{
  display : flex;
  align-items : center;
  justify-content : space-evenly;
  padding : 2rem 0rem;
}
.Container .Content .part1{
  padding-top : 1rem;
  font-size : 1.5rem;
  font-family : cursive;
  font-weight : bold;
}
.Container .Content .part1 .line{
  margin-bottom : 1rem;
}
.Container .Content .part1 .line .head{
  font-size : 2.5rem;
  color : var(--color1);
}
.Container .Content .part1 .button1{
  display : inline-block;
  text-decoration : none;
  margin-top : 2rem;
  font-size : 1.5rem;
  padding : 1rem;
  border : 0.25rem solid var(--color1);
  color : var(--color1);
  border-radius: 0.75rem;
}
.Container .Content .part1 .button1:hover{
  color : var(--color3);
  background-color: var(--color2);
  border-color: var(--color3);
}
.Container .Content .part2 .image1{
  padding : 1rem;
  width : 30rem; 
}

/* -------------- All Articles ----------------*/
.Allart{
  background-color : var(--color4);
  padding-bottom : 2rem;
}
.Allart .Title{
  color : var(--color3);
  margin : 0;
  padding : 1rem 0rem;
  font-size: 2.5rem;
}
.Allart .Box{
  display : block;
  margin-bottom : 2rem;
}
.Allart .article-preview{
  display : inline-block;
  width : 25%;
  padding : 1rem;
  margin : 0.5rem ;
  border : 0.25rem solid var(--color5); 
  background-color : var(--color5);
  border-radius : 10%;
}
.Allart .article-preview:hover{
  border : 0.25rem solid var(--color2);
  border-color : var(--color2);
}
.Allart .article-preview:active{
  background-color : var(--color4);
  border-color : var(--color6); 
}
.Allart .all-link{
  text-decoration: none;
}
.Allart .article-preview .all-link .article-title{
  font-size : 1.75rem;
  color : var(--color1);
}
.Allart .article-preview .all-link .article-author{
  font-size : 1.25rem;
  color : var(--color2);
}
/* ------------- Each Article --------------- */
.details{
  display : inline-block;
  width : 70%;
  position : relative;
}
.details .each-title{
  font-size : 2rem;
  padding-top: 1rem;
  text-decoration : 0.25rem underline var(--color1);
}
.details .each-author{
  color : var(--color1);
  font-weight : bold;
  text-align : right;
}
.details .each-body{
  padding : 1rem;
  margin-bottom : 2rem;
  text-align: left;
}
.details .update{
  text-decoration : none;
  border : 0.25rem solid var(--color1);
  padding : 1rem; 
  font-size : 1.5rem;
  color : var(--color3);
  background-color : var(--color2);
}
button.delete{
  display : block;
  min-width : 10rem;
  margin : 2rem auto;
  padding : 1rem; 
  font-size : 1.5rem;
  color : var(--color3);
  background-color : var(--color2);
  border : 0.25rem solid var(--color1);
}

/* --------------- Create Page --------------- */
.create{
  background-color : var(--color1);
  overflow: hidden;
}
.create .create-title{
  font-size : 2rem;
  font-weight : bold;
  padding : 1rem 0rem 1rem 0rem;
}
.create .create-author , .create .create-title2 , .create .create-body {
  font-size : 1.5rem;
}
.create input , .create textarea{
  padding : 0.25rem 0.5rem;
}
.create .add-article{
  margin : 1rem 0rem 1rem 0rem;
  font-size : 1.5rem;
  padding : 0.25rem;
  margin-bottom: 2rem;
}
/* -------------- Modify Pages --------------- */
.article-modify{
  background-color: var(--color1);
}
.article-modify .updating-article{
  padding : 1rem 0rem 1rem 0rem;
  font-size : 2rem;
  font-weight : bold;
}
.article-modify label{
  font-size : 1.5rem;
}
.article-modify input {
  padding : 0.25rem 0.5rem;
  font-size : 1rem;
}
.article-modify textarea{
  margin-top : 1rem;
  padding : 0.25rem 0.5rem;
  font-size : 1rem;
}
.article-modify .update-button{
  font-size : 2rem;
  background-color: var(--color1);
  margin-top : 2rem;
  margin-bottom : 5rem;
}

@media screen and (max-width : 830px) {
  body{
    font-size: 18px;
  }
  .create{
    margin-bottom: 1.75rem;
  }
}
@media screen and (max-width : 700px) {
  body{
    font-size: 16px;
  }
  .Container .Content{
    flex-direction: column-reverse;
    padding-top: 0rem;
  }
  .Container .Content .part2 .image1{
    width : 20rem;
    padding : 0.5rem;
  }
  .Container .Content .part1{
    margin-top: -2rem;
    padding-bottom: 0.5rem;
  }
  .Container .Content .part1 .button1{
    margin-top: 0rem;
  }
}
@media screen and (max-width) {
  .Container .Content .part1{
    margin-top: -2rem;
  }
}
@media screen and (max-width: 580px){
  body{
    font-size: 14px;
  }
  .header .logo{
    font-size: 2rem;
  }
  .Container .Content .part1{
    padding-top: 0px;
  }
}
@media screen and (max-width: 460px){
  .part1{
    font-size: 1rem;
    padding-left : 0.5rem;
    padding-right: 0.5rem;
  }
  .Container .Content .part1 .line{
    font-size: 1.25rem;
  }
  .Container .Content .part1 .line .head{
    font-size : 1.75rem;
  }
}
@media screen and (max-width: 440px){
  .header{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .menu{
    padding-left: 0rem;
  }
  .part1{
    padding-bottom: 0rem;
  }
  .Container .Content .part1 .button1{
    padding : 0.5rem;
  }
}
@media screen and (max-width : 350px){
  .part1{
    margin-bottom: 1rem;
    margin-top: -1rem;
  }
  .Container .Content .part1 .line{
    font-size: 1rem;
  }
  .Container .Content .part1 .line .head{
    font-size : 1.25rem;
  }
  .Container .Content .part2 .image1{
    padding: 0rem;
  }
}
@media screen and (max-width : 325px){
  .Container .Content .part2 .image1{
    width: 15rem;
  }
  .Container .Content .part1 {
    margin-top: 0.25rem;
  }
  .part2{
    padding-top: 1rem;
  }
  .Container .Content .part1 .button1{
    margin-top: 1rem;
  }
}
@media screen and (max-width: 240){
  .Container .Content .part2 .image1{
    width : 12rem;
  }
  .part1{
    margin-top: 0px;
  }
}